
.kanban-wrapper {
  overflow-x: auto;
}
.kanban {
  margin: 5px;
}
.kanban .kanban-header {
  margin-bottom:10px;
}
.kanban .kanban-lists {
  overflow: hidden;
}
.kanban .box-title {
  font-size: 16px;
}
.kanban-list {
  float: left;
  margin-left: 10px;
}
.kanban-list:first-child {
  margin-left: 0px;
}
.kanban-list .box-body {
  min-height: 30px;
  padding: 0 10px;
  overflow-y: auto;
}
.kanban-list .box-header {
  border:none;
}
.kanban-list .box-footer {
  border:none;
  background-color: transparent;
}
.kanban-card {
  background: #efe1c8;
  border-radius: 3px;
  overflow: hidden;
  padding: 6px 6px 2px 8px;
  position: relative;
  z-index: 10;
  margin-bottom: 10px;
}
.kanban-card:hover {
  background:#f7d497;
}
.kanban-card .kanban-card-title {
  clear: both;
  display: block;
  font-weight: 400;
  margin: 0 0 4px;
  overflow: hidden;
  text-decoration: none;
  word-wrap: break-word;
  color: black;
}
.kanban-list .kanban-card-add {
  text-decoration: none;
  font-size: 12px;
}
.sortable-fallback {
  -webkit-transform:rotate(3deg);
          transform:rotate(3deg);
}
.sortable-ghost {
  background: gray;
}

/*fix admin-lte box-tools*/
.box-header>.box-tools {
  top: 8px;
}



